.users {
  padding: 20px;
  text-align: center;
}

.item {
  margin-bottom: 40px;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.item a {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.leftCol {
  text-align: center;
  flex: 0 0 20%;
  max-width: 20%;
  background: #eee;
  padding: 20px 30px;
}

.photo {
  width: 140px;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto 10px;
}

.photo img {
  max-width: 100%;
  display: block;
  width: 100%;
}

.rightCol {
  display: flex;
  justify-content: space-between;
  flex: 0 0 68%;
  max-width: 68%;
  padding: 20px 30px;
  background: #ea3f36;
  color: #ffffff;
}

.fullName {
  font-size: 26px;
  font-weight: bold;
}

.country {
  font-size: 22px;
}

.button {
  font-size: 16px;
  border: none;
  cursor: pointer;
  color: #fff;
  padding: 7px 0;
  width: 155px;
  position: relative;
  z-index: 2;
}

button:disabled,
button[disabled] {
  opacity: .6;
  cursor: progress;
}

.button_red {
  background: #CC0000;
}

.button_gray {
  background: #303030;
}

.button_center {
  text-align: center;
  margin: 0 auto 20px auto;
  display: block;

}